@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
:root {
  /*     Common Colors     */
  --common-color: #00a0e9;
  /*     Font Size        */
  --fs-900: 9.375rem;
  --fs-800: 6.25rem;
  --fs-700: 3.5rem;
  --fs-600: 2rem;
  --fs-500: 1.75rem;
  --fs-400: 1.125rem;
  --fs-300: 1rem;
  --fs-200: 0.875rem;
  /*     Font Color       */
  --clr-white: 0 0% 100%;
}

.theme--light {
  --background: white;
  --clr-accent: 230 35% 7%;
  --clr-hover: 230 35% 7%;
  --clr-dark: 231 77% 90%;
}

.theme--dark {
  --background: black;
  --clr-accent: 231 77% 90%;
  --clr-hover: 230 35% 7%;
  --clr-light: 230 35% 7%;
}

/*      Font Utility Size Class     */
.fs-900 {
  font-size: var(--fs-900);
}

.fs-800 {
  font-size: var(--fs-800);
}

.fs-700 {
  font-size: var(--fs-700);
}

.fs-600 {
  font-size: var(--fs-600);
}

.fs-500 {
  font-size: var(--fs-500);
}

.fs-400 {
  font-size: var(--fs-400);
}

.fs-300 {
  font-size: var(--fs-300);
}

.fs-200 {
  font-size: var(--fs-200);
}

/*      Font Utility Color Class    */
.text-accent {
  color: hsl(var(--clr-accent)/0.7);
}

.text-hover {
  color: hsl(var(--clr-accent));
}

body {
  font-family: "Inter", sans-serif;
}

/* ------------------- */
/* Reset               */
/* ------------------- */
/* Box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Reset margins */
body,
h1,
h2,
h3,
h4,
h5,
p,
figure,
picture {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: 400;
}

/* set up the body */
body {
  font-size: var(--fs-400);
  color: hsl(var(--clr-white));
  background-color: hsl(var(--clr-dark));
  line-height: 1.5;
  min-height: 100vh;
}

/* make images easier to work with */
img,
picutre {
  max-width: 100%;
  display: block;
}

/* make form elements easier to work with */
input,
button,
textarea,
select {
  font: inherit;
}

/* remove animations for people who've turned them off */
@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
section {
  height: 400px;
  display: flex;
}
section .left-sec {
  width: 50%;
  height: 100%;
}
section .right-sec {
  width: 50%;
  height: inherit;
  background-image: url("https://nextui.org/gradient-right-dark.svg");
  background-size: cover;
}

.header {
  display: flex;
  height: 4rem;
  background: var(--background);
  color: hsl(var(--clr-accent)/0.7);
  font-size: var(--fs-400);
  font-weight: 600;
  justify-content: space-between;
}
.header .left-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 2rem;
}
.header .left-container .logo-wrapper {
  display: flex;
  font-size: var(--fs-500);
  font-weight: 800;
}
.header .left-container .logo-wrapper .logo {
  width: 3rem;
  height: 3rem;
}
.header .left-container .logo-wrapper .title {
  width: 12rem;
  height: 4rem;
}
.header .right-container {
  display: flex;
  margin: 0 2rem;
  overflow: hidden;
}
.header .right-container .navigationBar {
  display: flex;
}
.header .right-container .navigationBar .navBtn__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
}
.header .right-container .navigationBar .navBtn__wrapper .navBtn__link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
.header .right-container .navigationBar .navBtn__wrapper .navBtn__link:hover {
  color: hsl(var(--clr-accent));
}
.header .right-container .theme-switch {
  display: flex;
}
.header .right-container .theme-switch .switch-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .right-container .theme-switch .switch-wrapper button {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  opacity: 0.7;
  border-radius: 50%;
  position: relative;
}
.header .right-container .theme-switch .switch-wrapper button svg {
  width: 1.5rem;
  height: 1.5rem;
  fill: hsl(var(--clr-accent));
}
.header .right-container .theme-switch .switch-wrapper button svg .toggle-circle {
  transition: transform 250ms ease-out;
}
.header .right-container .theme-switch .switch-wrapper button::after {
  position: absolute;
  content: attr(aria-label);
  width: max-content;
  font-size: 0.8rem;
  color: var(--background);
  background: hsl(var(--clr-accent));
  right: -1.1rem;
  top: 120%;
  margin: 0 auto;
  padding: 0.3rem 0.6rem;
  border-radius: 5px;
  opacity: 0;
  transform: scale(0);
  transform-origin: 80% 0%;
  transition: transform 100ms linear 100ms, opacity 100ms linear;
  z-index: 100;
}
.header .right-container .theme-switch .switch-wrapper button:hover, .header .right-container .theme-switch .switch-wrapper button:focus-visible {
  opacity: 1;
  background: hsl(var(--clr-accent)/0.2);
}
.header .right-container .theme-switch .switch-wrapper button:hover::after, .header .right-container .theme-switch .switch-wrapper button:focus-visible::after {
  display: block;
  opacity: 0.8;
  transform: scale(1);
  transition: transform 70ms linear, opacity 100ms linear;
}

.theme--light svg .toggle-circle {
  transform: translateX(-15%);
}

.theme--light svg .toggle-sun {
  transform: rotate(0.5turn);
}

svg .toggle-sun {
  transform-origin: center center;
  transition: transform 500ms cubic-bezier(0.11, 0.14, 0.29, 1.32);
}

body {
  background: var(--background);
}